@import "../../../../../../../assets/sass/variables";

.softwareWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0;
  gap: 10px;
  width: 267px;
  height: 44px;

  .software__img {
    cursor: pointer;
  }

  .software__text {
    width: 233px;
    height: 44px;

    font-style: normal;
    text-align: left;
    cursor: pointer;

    &:hover {
      color: $btnBgHover;
    }

    &:active {
      color: $btnBgActive;
    }
  }
}

