@import "../../../../assets/sass/variables";

.registryNewsWrapper {
  display: flex;
  width: 1400px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 80px 115px;
  text-align: left;
  min-height: 1100px;
  background-color: $bgSecondary;

  .registryNews__title {
    width: 100%;
    text-align: left;
    font-style: normal;
    font-weight: 700;
    font-size: $titleSize;
    line-height: 49px;
    color: $titlePrimary;

  }

  .registryBlock {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0;
    gap: 30px;
    flex-wrap: wrap;
    width: 100%;
    height: 754px;
  }
}